<template>
  <div id="svg_manager">
    <div class="projects-section">
      <div class="projects-section-header">
        <p >图包管理</p>
        <p class="add_package" @click="addPackage">+</p>
      </div>
      <div class="projects-section-line">
        <div class="projects-status">
          <div class="item-status">
            <span class="status-number">{{likeCount}}</span>
            <span class="status-type">总赞数</span>
          </div>
          <div class="item-status">
            <span class="status-number">{{collectionCount}}</span>
            <span class="status-type">总收藏数</span>
          </div>
          <div class="item-status">
            <span class="status-number">{{count}}</span>
            <span class="status-type">总图包数</span>
          </div>
        </div>
        <div class="view-actions">
          <button class="view-btn list-view" title="List View">
            <label for="select">
              <input type="checkbox" @change="isAllChecked" v-model="allChecked" id="select">
              <span> 全选</span>
            </label>
          </button>
          <button class="view-btn list-view" title="List View" v-show="isEdit" @click="editPackage">
            <svg  viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="1093" class="icon" style="width: 1em;height: 1em;vertical-align: middle;fill: currentColor;overflow: hidden;" ><path data-v-4fbacbf7="" d="M684.202667 117.248c15.893333-15.872 42.154667-15.36 58.922666 1.408l90.517334 90.517333c16.661333 16.661333 17.344 42.986667 1.429333 58.922667l-445.653333 445.653333c-7.936 7.914667-23.104 16.746667-34.218667 19.776l-143.701333 39.253334c-21.909333 5.994667-35.114667-7.104-29.568-28.949334l37.248-146.773333c2.773333-10.944 11.562667-26.346667 19.392-34.176l445.653333-445.653333zM268.736 593.066667c-2.901333 2.901333-8.106667 12.074667-9.130667 16.021333l-29.12 114.773333 111.957334-30.570666c4.437333-1.216 13.632-6.549333 16.810666-9.728l445.653334-445.653334-90.517334-90.496-445.653333 445.653334zM682.794667 178.986667l90.517333 90.517333-30.186667 30.186667-90.496-90.517334 30.165334-30.165333z m-362.026667 362.048l90.496 90.517333-30.165333 30.165333-90.517334-90.496 30.165334-30.186666zM170.666667 874.666667c0-11.776 9.429333-21.333333 21.461333-21.333334h661.077333a21.333333 21.333333 0 1 1 0 42.666667H192.128A21.333333 21.333333 0 0 1 170.666667 874.666667z" p-id="1094"></path>
            </svg> 编辑
          </button>
          <button class="view-btn grid-view active" title="Grid View" v-show="isDelete" @click="delVisible = true">
            <svg class="icon" style="width: 1em;height: 1em;vertical-align: middle;fill: currentColor;overflow: hidden;" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="4612"><path d="M394.159522 868.978408c21.387884 0 38.74028-17.344333 38.74028-38.753382L432.899802 494.386567c0-21.397963-17.352396-38.750359-38.74028-38.750359-21.397963 0-38.763461 17.352396-38.763461 38.750359l0 335.838458C355.396061 851.633067 372.761559 868.978408 394.159522 868.978408zM962.498117 145.634346l-64.571837 0L610.479409 145.634346c2.125585-8.257431 3.255401-16.914983 3.255401-25.836596 0-57.074336-46.262978-103.337314-103.332274-103.337314-57.07232 0-103.335298 46.262978-103.335298 103.337314 0 8.921613 1.130824 17.578158 3.255401 25.836596L122.886853 145.634346 58.316024 145.634346c-21.408041 0-38.750359 17.342317-38.750359 38.750359 0 21.397963 17.342317 38.750359 38.750359 38.750359l64.571837 0 0 620.006748c0 99.881348 80.970794 180.832992 180.839039 180.832992l413.355302 0c99.855143 0 180.846094-80.952652 180.846094-180.832992L897.928296 223.135064l64.571837 0c21.408041 0 38.74028-17.352396 38.74028-38.750359C1001.238397 162.976664 983.907166 145.634346 962.498117 145.634346zM510.403543 93.964178c14.265308 0 25.841635 11.566248 25.841635 25.833573 0 14.270348-11.576327 25.836596-25.841635 25.836596s-25.831557-11.56524-25.831557-25.836596C484.571986 105.529418 496.138235 93.964178 510.403543 93.964178zM820.413468 791.474667c0 85.613016-69.40253 155.007482-155.004459 155.007482L355.396061 946.482149c-85.579756 0-154.99438-69.394467-154.99438-155.007482L200.401681 223.135064l620.011787 0L820.413468 791.474667zM626.657643 868.978408c21.395947 0 38.750359-17.344333 38.750359-38.753382L665.408002 494.386567c0-21.397963-17.35542-38.750359-38.750359-38.750359-21.408041 0-38.753382 17.352396-38.753382 38.750359l0 335.838458C587.904261 851.633067 605.249602 868.978408 626.657643 868.978408z" p-id="4613"></path>
            </svg>
            删除
          </button>
        </div>
      </div>
      <div>
        <div class="content-section">
          <div class="apps-card" v-if="packageList.length != 0">
            <div class="app-card" v-for="item in packageList" :key="item.id">
              <span>
                <span class="package_name"  @click="goToPage(item.id,item.package_name)">{{ item.package_name }}</span>
                <input type="checkbox" v-model="item.isChecked" @change="check(item.id)">
              </span>
              <div class="app-card__subtext"  @click="goToPage(item.id,item.package_name)">
                <svg-item :svgList="item.svgs" :section="'manage' + item.id" />
              </div>
              <div class="app-card-buttons">
                <div class="left_items">
                  <span class="like">
                    <svg
                      class="icon"
                      style="
                        width: 1.5em;
                        height: 1.5em;
                        vertical-align: text-bottom;
                        fill: currentColor;
                        overflow: hidden;
                      "
                      viewBox="0 0 1024 1024"
                      version="1.1"
                      xmlns="http://www.w3.org/2000/svg"
                      p-id="2461"
                    >
                      <path
                        d="M725.333333 192c-89.6 0-168.533333 44.8-213.333333 115.2C467.2 236.8 388.266667 192 298.666667 192 157.866667 192 42.666667 307.2 42.666667 448c0 253.866667 469.333333 512 469.333333 512s469.333333-256 469.333333-512c0-140.8-115.2-256-256-256z"
                        fill="#F44336"
                        p-id="2462"
                      ></path>
                    </svg>&nbsp;
                    {{ item.like_num }}
                  </span>
                  <span>
                    <img src="@/assets/img/collections.svg" alt="" width="25" height="25">&nbsp;{{item.like_num}}
                  </span>
                </div>
                <div>
                  <p class="count">数量 {{ item.count }}</p>
                  <!-- <p class="tag">
                    {{ item.package_tag }}
                  </p> -->
                  
                </div>
              </div>
            </div>
            
          </div>
          <div class="no_content" v-else>
            <svg class="icon" style="vertical-align: middle;overflow: hidden;" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="5331"><path d="M624.76 961.298H391.153a6.614 6.614 0 0 1-6.612-6.61 6.61 6.61 0 0 1 6.612-6.606h233.605a6.61 6.61 0 0 1 6.612 6.606 6.612 6.612 0 0 1-6.612 6.61zM601.66 153.254c-7.511 0-14.305-5.153-16.078-12.782l-13.118-56.32c-2.07-8.89 3.458-17.77 12.343-19.84 8.89-2.07 17.771 3.457 19.841 12.343l13.117 56.325c2.07 8.89-3.454 17.77-12.343 19.84a16.75 16.75 0 0 1-3.762 0.434z m183.91 145.25c-1.088 0-2.188-0.104-3.292-0.33l-56.686-11.447c-8.943-1.805-14.73-10.521-12.927-19.467 1.81-8.946 10.525-14.73 19.47-12.925l56.688 11.446c8.941 1.805 14.73 10.525 12.925 19.468-1.588 7.844-8.476 13.256-16.178 13.256zM695.21 184.91c-4.759 0-9.481-2.042-12.749-5.996-5.809-7.037-4.818-17.454 2.215-23.264l48.609-40.159c7.036-5.815 17.448-4.822 23.263 2.214 5.815 7.033 4.818 17.448-2.214 23.264l-48.609 40.158a16.464 16.464 0 0 1-10.515 3.783z m159.61 729.905H465.117l273.162-99.01c16.922-6.132 30.443-18.488 38.076-34.792 7.629-16.305 8.45-34.605 2.318-51.529l-17.71-48.85c-2.174-6.005-8.81-9.11-14.813-6.931-6.006 2.174-9.11 8.81-6.932 14.813l17.704 48.849c4.027 11.116 3.489 23.132-1.521 33.844-5.01 10.706-13.892 18.822-25.008 22.849L422.989 905.483c-11.122 4.026-23.138 3.487-33.845-1.523-10.707-5.014-18.823-13.89-22.85-25.008L196.442 410.341c-8.315-22.94 3.584-48.375 26.53-56.69l307.406-111.428c11.116-4.027 23.132-3.484 33.843 1.526 10.707 5.01 18.823 13.892 22.847 24.99l69.38 192.09c2.17 6.01 8.797 9.12 14.81 6.95 6.005-2.17 9.114-8.798 6.944-14.804L608.816 260.87c-6.133-16.922-18.488-30.443-34.792-38.076-16.306-7.629-34.606-8.45-51.528-2.318l-307.41 111.424c-34.937 12.665-53.055 51.389-40.394 86.324l169.853 468.609c8.262 22.793 27.611 38.414 49.767 43.016a8.114 8.114 0 0 0 4.44 1.318h5.793c1.14 0.056 2.279 0.1 3.427 0.1 1.144 0 2.288-0.039 3.436-0.1h443.414c4.518 0 8.176-3.662 8.176-8.177s-3.66-8.176-8.177-8.176z m-80.245-494.862L756.61 410.2c-7.175-3.887-17.448 1.175-22.954 11.317L584.131 697.114c-0.666 1.227-1.01 2.58-0.965 3.823l0.978 30.492c-0.465 0.03-0.931 0.125-1.387 0.3l-46.448 17.309a4.956 4.956 0 1 0 3.461 9.29l44.706-16.662 0.012 0.356c0.11 3.362 2.874 4.862 5.749 3.118l35.162-21.289c1.065-0.644 2.014-1.666 2.68-2.893l149.524-275.6c5.502-10.136 4.149-21.514-3.027-25.405z m-334.26 364.87l-34.088 12.703a4.954 4.954 0 0 0-2.913 6.375 4.957 4.957 0 0 0 6.375 2.914l34.087-12.704a4.956 4.956 0 0 0 2.915-6.375 4.95 4.95 0 0 0-6.377-2.913z m71.22-26.552l-46.448 17.314a4.955 4.955 0 0 0-2.914 6.375 4.957 4.957 0 0 0 6.375 2.914l46.448-17.312a4.953 4.953 0 0 0 2.914-6.377 4.953 4.953 0 0 0-6.376-2.914z m-136.713-76.617a4.96 4.96 0 0 1-4.65-3.228 4.96 4.96 0 0 1 2.913-6.375l29.148-10.864a4.957 4.957 0 0 1 3.462 9.289l-29.147 10.864a4.9 4.9 0 0 1-1.726 0.314z m53.916-20.098c-2.01 0-3.901-1.236-4.645-3.232a4.956 4.956 0 0 1 2.913-6.376l46.448-17.314a4.956 4.956 0 0 1 6.375 2.913 4.953 4.953 0 0 1-2.913 6.377l-46.448 17.314a4.914 4.914 0 0 1-1.73 0.318z m71.219-26.546a4.963 4.963 0 0 1-4.644-3.227 4.953 4.953 0 0 1 2.913-6.376l46.448-17.319a4.956 4.956 0 0 1 6.375 2.914 4.953 4.953 0 0 1-2.913 6.376l-46.449 17.314a4.947 4.947 0 0 1-1.73 0.318z m71.22-26.552a4.957 4.957 0 0 1-1.73-9.603l46.447-17.313a4.955 4.955 0 0 1 6.376 2.913 4.954 4.954 0 0 1-2.913 6.375l-46.448 17.315a4.953 4.953 0 0 1-1.731 0.313z m0 0" p-id="5332"></path></svg>
            <span>暂无数据</span>
          </div>
          <div class="page_bar">
            <Page :current="page" :total="count" :page-size="12" @on-change="pageChange" class-name="page" simple></Page>
          </div>
        </div>
      </div>
    </div>
    <modal :title="modalType == 'add'?'新增图包':'编辑图包'" :visible="visible" @cancel="cancel" @confirm="confirm" :width="500">
      <Row class="edit_row">
        <Col span="6" >图包名称</Col>
        <Col span="18"><Input v-model="edit.name" placeholder="请输入..." :maxlength="16"></Input></Col>
      </Row>
      <Row class="edit_row taglist">
        <Col span="6">图包标签</Col>
        <Col span="18">
          <span class="tag" v-for="(item, index) in defaultTag" :key="item.id" v-show="modalType == 'add'">
            <span @click="selectTag(index)" :class="{'selected': item.isSelect}">{{item.tag}}</span>
          </span>
          <span v-for="(item, index) in edit.tagList" :key="index + '-'" class="tag">
            <span>{{item}} <span @click="delTag(index)" style="cursor: pointer">x</span></span>
          </span>
          <span style="cursor: pointer;white-space: nowrap;" @click="addTag" class="tag">
            <span>自定义 +</span>
          </span>
        </Col>
      </Row>
      <Row class="edit_row">
        <Col span="6">图包描述</Col>
        <Col span="18"><Input v-model="edit.description" type="textarea" :autosize="{ minRows: 6, maxRows: 6 }" placeholder="请输入..." :maxlength="64" resize="none"></Input></Col>
      </Row>
    </modal>
    <modal :title="'自定义标签'" :visible="tagVisible" @cancel="tagCancel" @confirm="tagConfirm">
      <Input v-model="newTag" placeholder="请输入..." :maxlength="4"></Input>
    </modal>
    <modal :title="'删除提示'" :visible="delVisible" @cancel="delCancel" @confirm="delConfirm">
      确定删除文件吗！删除后将不能恢复！
    </modal>
  </div>
</template>

<script>

import {getSvgPackageList, updateSvgPackageInfo, AddSvgPackage, DelSvgPackage} from '@/network/svg/index'
import {likeCount, packageCollectionCount} from '@/network/operation/index'
import SvgItem from '@/components/SvgItem.vue'
import Modal from '@/components/Modal'
export default {
  data() {
    return {
      packageList: [],
      uid: '',
      page: 1,
      size: 12,
      count: 0,
      likeCount: 0,
      collectionCount: 0,
      isEdit: false,
      isDelete: false,
      allChecked: false,
      visible: false,
      modalTitle: '',
      modalType: '',
      edit: {
        id: '',
        name: '',
        tagList: [],
        description: ''
      },
      tagVisible: false,
      newTag: '',
      defaultTag: [
        {id: 1, tag: '纯SVG', isSelect: false},
        {id: 2, tag: '混合', isSelect: false},
        {id: 3, tag: '循环', isSelect: false},
        {id: 4, tag: '自动', isSelect: false},
        {id: 5, tag: '交互', isSelect: false},
      ],
      delVisible: false
    }
  },
  components: {
    SvgItem,
    Modal
  },
  created() {
    const info = JSON.parse(localStorage.getItem('userInfo'))
     if (info) this.uid = info.id
    this.getPackage()
    this.getCollectionCount()
    this.getLikeCount()
  },
  methods: {
    // 获取图包
    getPackage() {
      this.allChecked = false
      this.isEdit = false
      this.isDelete = false
      getSvgPackageList(this.uid, 8, this.page, this.size).then(res => {
        if (res.status == 200) {
          const data = res.data
          this.packageList = data.data
          this.page = data.page
          this.count = data.count
          this.packageList.map((item, index) => {
            item.isChecked = false
          })

        } else if (res.status == 204) {
          this.packageList = []
        } else {
          this.$toast.show("请求失败，请重试");
        }
      }).catch(err => {
        this.$toast.show("请求错误，请重试");
      })
    },
    getCollectionCount() {
      packageCollectionCount().then(res => {
        if (res.status == 200) {
          this.collectionCount = res.data.count
        } else {
          this.$toast.show('请求失败，请重试');
        }
      }).catch(err => {
        this.$toast.show(err.response.data.message || "请求错误，请重试");
      })
    },
    getLikeCount() {
      likeCount().then(res => {
        if (res.status == 200) {
          this.likeCount = res.data.count
        } else {
          this.$toast.show('请求失败，请重试');
        }
      }).catch(err => {
        this.$toast.show(err.response.data.message || "请求错误，请重试");
      })
    },
    // 多选
    check() {
      let list = this.packageList.filter(item => {return item.isChecked == true})
      if (list.length == 1) {
        this.edit.id = list[0].id
        this.isEdit = true
        this.isDelete = true
      } else if (list.length > 1) {
        this.isEdit = false
      } else {
        this.isEdit = false
        this.isDelete = false
      }
      if (list.length === this.packageList.length) {
        this.allChecked = true
      } else {
        this.allChecked = false
      }
    },
    // 全选/不全选
    isAllChecked() {
      this.isEdit = false
      let list = this.packageList.filter(item => {return item.isChecked == true})
      if (list.length == this.packageList.length) {
        this.packageList.map(item => {
          item.isChecked = false
          this.isDelete = false
        })
      } else {
        this.packageList.map(item => {
          item.isChecked = true
          this.isDelete = true
        })
      }
    },
    // 翻页
    pageChange(current) {
      this.page = current
      this.getPackage()
    },
    // 编辑图包信息
    editPackage() {
      this.visible = true
      this.modalType = 'edit'
      this.packageList.forEach(item => {
        if (item.id == this.edit.id) {
          this.edit.name = item.package_name
          this.edit.tagList = item.package_tag.split(' ')
          this.edit.description = item.package_description
        }
      })
      
    },
    cancel() {
      this.visible = false
    },
    // 新增/编辑
    confirm() {
      if (this.modalType == 'add') {
        this.addConfirm()
      } else {
        this.editConfirm()
      }
      
    },
    // 编辑信息提交
    editConfirm() {
      const {id, name, description, tagList} = this.edit
      let tag = String(tagList).replace(/\,/g, ' ')
      updateSvgPackageInfo(id, name, description, tag).then(res => {
        this.visible = false
        if (res.status == 200) {
          this.visible = false
          this.$toast.show('修改成功');
        } else {
          this.$toast.show('请求失败，请重试');
        }
      }).catch(err => {
        this.$toast.show(err.response.data.message || "请求错误，请重试");
      })
    },
    // 新增标签
    addTag() {
      if (this.modalType == 'add') {
        let defaultTagLen= this.defaultTag.filter(item => {return item.isSelect == true}).length
        if (defaultTagLen + this.edit.tagList.length > 5) {
          this.$toast.show('不能超过6个')
          return
        }
      } else {
        if (this.edit.tagList.length > 5) {
          this.$toast.show('不能超过6个')
          return
        }
        
      }
      this.newTag = ''
      this.tagVisible = true
    },
    tagCancel() {
      this.tagVisible = false
    },
    // 新增标签确定
    tagConfirm() {
      if(this.newTag == '') {
        this.$toast.show('标签名不能为空')
        return
      }
      const isExists = this.edit.tagList.some(item => {return item === this.newTag})
      if (isExists) {
        this.$toast.show('标签名重复')
        return
      }
      this.edit.tagList.push(this.newTag)
      this.tagVisible = false
    },
    // 新增标签删除
    delTag(index) {
      this.edit.tagList.splice(index, 1)
    },
    // 新增图包模态框
    addPackage() {
      this.visible = true
      this.edit.id = ''
      this.edit.name = ''
      this.edit.description = ''
      this.edit.tagList = []
      this.modalType = 'add'
      this.defaultTag.map(item => item.isSelect = false)
      this.getPackage()
    },
    // 选择默认标签
    selectTag(index) {
      let defaultTagLen= this.defaultTag.filter(item => {return item.isSelect == true}).length
      if (defaultTagLen + this.edit.tagList.length > 5) {
        this.$toast.show('不能超过6个')
        return
      }
      this.defaultTag[index].isSelect = !this.defaultTag[index].isSelect
      this.tagList
    },
    // 新增图包信息提交
    addConfirm() {
      const {name, description, tagList} = this.edit
      const allTag = String(this.defaultTag.filter(item => {return item.isSelect == true}).map(item => {return item.tag}).concat(tagList)).replace(/\,/g, ' ')
      AddSvgPackage(name, allTag, description).then(res => {
        this.visible = false
        if (res.status == 201) {
          this.$toast.show('添加成功');
          this.getPackage()
        } else {
          this.$toast.show('请求失败，请重试');
        }
      }).catch(err => {
        this.$toast.show(err.response.data.message || "请求错误，请重试");
      })
    },
    delCancel() {
      this.delVisible = false
    },
    // 删除图包
    delConfirm() {
      this.packageList.filter(item => {return item.isChecked == true}).map(item => {
        DelSvgPackage(item.id).then(res => {
          this.delVisible = false
          if (res.status == 200) {
            this.$toast.show('删除成功');
            this.getPackage()
          } else {
            this.$toast.show('请求失败，请重试');
          }
        }).catch(err => {
          this.$toast.show(err.response.data.message || "请求错误，请重试");
        })
      })
    },
    goToPage(pid, pName) {
      this.$EventBus.$emit('getPid', pid)
      const exist = this.$store.state.menuList.some(item => {return item.sign === 'mine'})
      if (exist) {
        for (let item of this.$store.state.menuList) {
          if (item.sign === 'mine') {
            item.pid = pid
            item.title = `图包-${pName}`
            item.active = true
          } else {
            item.active = false
          }
        }
      } else {
        this.$store.state.menuList.forEach(item => {
          item.active = false
        })
        this.$store.commit('SETMENULIST', {pid: pid,title: `图包-${pName}`, route: 'svgList', active: true, sign: 'mine'})
      }
      this.$router.push({name: 'svgList', params: {pid}}).catch(err => {})
      // this.$router.push({name: 'svgList', params: {pid}}).catch(err => {})
      // this.$store.commit('SETMENULIST', {title: `图包管理`, route: 'svgManager', active: true, sign: 'mine'})
      // this.$router.push({name: 'svgList'})
    }
  },
  
};
</script>

